.dropdown {
  margin: auto auto;
  max-width: 20vw;
  font-size: calc(1.375rem + 1.5vw);
  color: #2F5588;
}
.Dropdown-root {
  position: relative;
}

.Dropdown-control {
  background: linear-gradient(90deg, #3795F2 24.68%, #052A5E 108.23%) padding-box,
              linear-gradient(90deg, #3795F2 24.68%, #052A5E 108.23%) border-box;

  position: relative;
  overflow: hidden;
  background-color: white;
  border: 5px solid transparent;
  border-radius: 60px;
  box-sizing: border-box;
  color: WHITE;
  cursor: pointer;
  outline: none;
  transition: all 200ms ease;
}

.Dropdown-control:hover {
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
}

.is-open .Dropdown-arrow {
  border-color: transparent transparent #999;
  border-width: 0 5px 5px;
}

.Dropdown-menu {
  background: linear-gradient(90deg, #3795F2 24.68%, #052A5E 108.23%) padding-box,
              linear-gradient(90deg, #3795F2 24.68%, #052A5E 108.23%) border-box;
  background-color: white;
  border: 5px solid transparent;
  border-radius: 15px;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
  box-sizing: border-box;
  margin-top: -1px;
  max-height: 200px;
  overflow-y: auto;
  position: absolute;
  top: 110%;
  width: 100%;
  z-index: 1000;
  -webkit-overflow-scrolling: touch;
  transition: all 200ms ease;
}

.Dropdown-menu .Dropdown-group > .Dropdown-title{
  padding: 8px 10px;
  color: #323E67;
  font-weight: bold;
  text-transform: capitalize;
}

.Dropdown-option {
  box-sizing: border-box;
  color: rgba(51, 51, 51, 0.5);
  cursor: pointer;
  display: block;
  padding: 8px 10px;
  transition: all 200ms ease;
}

.Dropdown-option:last-child {
  border-bottom-right-radius: 2px;
   border-bottom-left-radius: 2px;
}

.Dropdown-option:hover {
  background-color: #313ea5;
  color: white;
}

.Dropdown-option.is-selected {
  background-color: #313ea5;
  color: white;
}

.Dropdown-noresults {
  box-sizing: border-box;
  color: #ccc;
  cursor: default;
  display: block;
  padding: 8px 10px;
}

