  .basic-grid {
    display: grid;
    gap: 1rem;
    width: 80vw;
    margin: auto auto;
    grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
  }
  .card-tall {
    grid-row: span 2 / auto;
  }
  .card-wide {
    grid-column: span 2 / auto;
  }
  .card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #e7e7e7;
    color: #fff;
    box-shadow: rgba(3, 8, 20, 0.1) 0px 0.15rem 0.5rem, rgba(2, 8, 20, 0.1) 0px 0.075rem 0.175rem;
    height: 100%;
    width: 100%;
    border-radius: 4px;
    transition: all 500ms;
    overflow: hidden;
 }
.card:hover {
    box-shadow: rgba(2, 8, 20, 0.1) 0px 0.35em 1.175em, rgba(2, 8, 20, 0.08) 0px 0.175em 0.5em;
    transform: translateY(-3px) scale(1.05);
}
.card img {
  transition: all 500ms;
  position: center;
  object-fit: cover;
  min-width: 100%;
  max-height: 100%;
  max-width: 100%;
}
.card:hover img {
    -webkit-filter: blur(5px);
    -moz-filter: blur(5px);
    -o-filter: blur(5px);
    -ms-filter: blur(5px);
    filter: blur(5px);
 }
 .card p {
    visibility: hidden;
    opacity: 0;
    transition: all 500ms;
    width: 90%;
    text-align: center;
    text-shadow: 1px 1px 3px #353535;

    position: absolute;
    top: 50;
    left: 50;
 }
 .card:hover p {
    visibility: visible;
    opacity: 1;
  }

